/* eslint-disable require-atomic-updates */
// @ is an alias to /src
import { Component, Vue } from "vue-property-decorator";
import MobileVerify from "@/components/otp/MobileVerify.vue";
import { FirestoreService } from "@/services/FirestoreService";
import CarouselInfo from "@/dto/CarouselInfo";
import GiftCardItemInfo from "@/dto/GiftCardItemInfo";
import { Section } from "@/dto/Section";
import { ProductInfo } from "@/dto/ProductInfo";
import { firestoreDb } from "@/firebaseInit";
import { collection, getDocs, limit, orderBy, query, startAfter, where } from "firebase/firestore";
import { CartOrder } from "@/dto/CartOrder";

@Component({
  components: {
    MobileVerify,
  }
})
export default class Home extends Vue {
  carsoulInfo: CarouselInfo = new CarouselInfo();
  giftCardInfo: GiftCardItemInfo = new GiftCardItemInfo();
  productSections: Section[] = [];
  categories = [
    { name: "action", src: "action.svg", link: "category/action" },
    { name: "Adventure", src: "adventure.svg", link: "category/adventure" },
    { name: "FPS/TPS", src: "fps.svg", link: "category/fps" },
    { name: "RPG", src: "rpg.svg", link: "category/rpg" },
    { name: "Indie", src: "indie.svg", link: "category/indie" },
    { name: "Simulation", src: "simulation.svg", link: "category/simulation" },
    { name: "Strategy", src: "strategy.svg", link: "category/strategy" },
    { name: "Racing", src: "racing.svg", link: "category/racing" },
    { name: "Fighting", src: "fighting.svg", link: "category/fighting" },
    { name: "Sport", src: "sport.svg", link: "category/sport" },
    {
      name: "Hack & slash",
      src: "hack-slash.svg",
      link: "category/hack-slash"
    },
    { name: "Puzzle", src: "puzzle.svg", link: "category/puzzle" },
    { name: "Platform", src: "platform.svg", link: "category/platform" },
    { name: "Arcade", src: "arcade.svg", link: "category/arcade" },
    { name: "MMO", src: "mmo.svg", link: "category/mmo" },
    {
      name: "Point & Click",
      src: "point-click.svg",
      link: "category/point-click"
    },
    {
      name: "Educational",
      src: "educational.svg",
      link: "category/educational"
    },
    { name: "Music", src: "music.svg", link: "category/music" }
  ];

  sections = [
    "https://cdn.eneba.com/9b35a9095c42fb593f03.png",
    "https://cdn.eneba.com/77b7dedacd3c7ea3e236.png",
    "https://cdn.eneba.com/d79e731246e2a3ef97ea.png"
  ];

  testing() {
    console.log("clicked");

  }
  async mounted() {
    await this.getCarouselDetails();
    await this.getGiftCardDetails();
    await this.getSectionsDetails();
  }
  openImage(props: any) {
    try {
      if (
        this.$vuetify.breakpoint.name === "sm" ||
        this.$vuetify.breakpoint.name === "xs"
      ) {
        console.log("small");
      }
      this.carsoulInfo.carousel.forEach((item: any) => {
        if (item.id === props.id) {
          item.isExpanded = true;
        } else {
          item.isExpanded = false;
        }
      });
    } catch (error) {
      console.log(error);
    }
  }

  async getCarouselDetails() {
    try {
      const snapshot = await FirestoreService.getSingleDocument<CarouselInfo>(
        "HomeScreen",
        "CarouselV2",
        CarouselInfo.converter
      );
      if (snapshot !== null) {
        this.carsoulInfo = snapshot as CarouselInfo;
        this.carsoulInfo.carousel[0].isExpanded = true;
      } else {
        Vue.toasted.show("failed to get carousel info", {
          icon: "face",
          type: "error"
        });
      }
    } catch (error) {
      //  Vue.toasted.show('Error fetching Carousel Details', { icon: 'face', type: 'error' });
      console.log(error);
    }
  }
  async getGiftCardDetails() {
    try {
      const snapshot = await FirestoreService.getSingleDocument<GiftCardItemInfo>(
        "HomeScreen",
        "GiftCards",
        GiftCardItemInfo.converter
      );
      if (snapshot !== null) {
        this.giftCardInfo = snapshot as GiftCardItemInfo;
      }
    } catch (error) {
      Vue.toasted.show("Error fetching GiftCards Details", {
        icon: "face",
        type: "error"
      });
      console.log(error);
    }
  }
  async getSectionsDetails() {
    try {
      const snapshot = await FirestoreService.getDocuments('ProductSections', Section.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.productSections.push(doc.data());
        });
        for (let i = 0; i < this.productSections.length; i++) {
          const intialProducts = await this.getProductsDetails(
            this.productSections[i].id
          );
          if (intialProducts.length < 10) {
            this.productSections[i].loadMore = false;
          } else {
            this.productSections[i].loadMore = true;

          }
          this.productSections[i].products = intialProducts;
        }
      } else {
        Vue.toasted.show('Error Getting sections', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show('Error fetching Section Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
  }
  async getProductsDetails(sectionId: string) {
    try {
      console.log(sectionId);
      const sectionItems: ProductInfo[] = [];
      const snapshot = await FirestoreService.getDocuments(`ProductSections/${sectionId}/products`, ProductInfo.converter, 10);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          sectionItems.push(doc.data());
        });
      }
      console.log(sectionItems);
      return sectionItems;
    } catch (error) {
      Vue.toasted.show('Error fetching section Products Details', { icon: 'face', type: 'error' });
      throw new Error(error);
    }
  }
  async getPaginatedProductsDetails(section: Section) {
    try {
      section.loading = true;
      const sectionItems: ProductInfo[] = [];
      const q = query(collection(firestoreDb, `ProductSections/${section.id}/products`).withConverter<ProductInfo>(ProductInfo.converter), orderBy("id"), startAfter(section.products[section.products.length - 1].id), limit(10));
      const resp = await getDocs(q);
      if (!resp.empty) {
        resp.forEach((doc: any) => {
          sectionItems.push(doc.data());
        });
        if (sectionItems.length < 10) {
          section.loadMore = false;
        } else {
          section.loadMore = true;
        }
        section.products.push(...sectionItems);
        console.log(sectionItems);
      }

    } catch (error) {
      Vue.toasted.show('Error fetching section Products Details', { icon: 'face', type: 'error' });
      throw new Error(error);
    } finally {
      section.loading = false;
    }
  }
  getLowestPrice(item: ProductInfo) {
    return item.sellerDetails.find(
      (item) => item.lowestPrice === true
    )?.price;
  }
}
